.container {
    background-color : rgb(201, 218, 154);
    padding-top : 2vh;
    padding-bottom : 2vh;
    padding-left : 1vw;
    padding-right : 1vw;
    border : 2px solid black;
    border-radius: 15px;
    width : 93vw;
    height : 10vh;
    display : flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin : 20px;
}

.namebutton {
    display:flex;
    flex-direction : column;
}

.name {
    background-color: rgb(229, 255, 0);
    padding-left: 1vw;
    padding-right : 1vw;
    padding-top : 0.1vh;
    padding-bottom : 0.1vh;
    border : 2px solid rgb(0, 0, 0);
    border-radius : 10px;
    color : black;
    font-size: 2.3vw;
}

.status {
    background-color: rgb(133, 231, 133);
    font-size : 2.3vw;
    border : 2px solid black;
    border-radius : 8vw;
    height: 8vh;
    width: 8vw;
    align-items: center;
    display : flex;
    justify-content: center;
}

.buttoncontainer {
    margin-top : 20px;
}

.button {
    background-color : rgb(255, 255, 255);
    margin-top: 20px;
    width : 100px;
    border-radius : 10px;
}