.container2 {
    background-color : rgb(233, 164, 107);
    padding-top : 2vh;
    padding-bottom : 2vh;
    padding-left : 1vw;
    padding-right : 1vw;
    border : 2px solid black;
    border-radius: 15px;
    width : 93vw;
    height : 10vh;
    display : flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin : 20px;
}

.namebutton2 {
    display:flex;
    flex-direction : column;
}

.name2 {
    background-color: rgb(222, 231, 143);
    padding-left: 1vw;
    padding-right : 1vw;
    padding-top : 0.1vh;
    padding-bottom : 0.1vh;
    border : 2px solid rgb(0, 0, 0);
    border-radius : 10px;
    color : black;
    font-size: 2.3vw;
}

.status2 {
    background-color: rgb(134, 128, 219);
    font-size : 2.3vw;
    border : 2px solid black;
    border-radius : 8vw;
    height: 8vh;
    width: 8vw;
    align-items: center;
    display : flex;
    justify-content: center;
}

.buttoncontainer2 {
    margin-top : 20px;
}

.button2 {
    background-color : rgb(255, 255, 255);
    margin-top: 20px;
    width : 100px;
    border-radius : 10px;
}